.form_present {
	position: relative;
	width: 100%;
	// overflow: hidden;

	// height: rem(543);
	// background: url(../img/section5/message_bg.png) no-repeat top center;
	background-size: cover;
	background: linear-gradient(180deg, rgba(254, 255, 255, 0) 0%, white 100%), url(../img/section5/message_bg.png) no-repeat top center;
	// margin-bottom: rem(248);
	padding-bottom: rem(210);

	&::before {
		content: "";
		position: absolute;
		bottom: rem(-120);
		right: 0;
		width: rem(380);
		height: rem(743);
		z-index: 2;
		background: url(../img/section5/right_bg.png) no-repeat;
	}

	>.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
	}

	&__left {
		margin-left: rem(100);
	}

	&__right {
		position: absolute;
		top: rem(35);
		right: rem(32);

		img {
			
		}
	}
}